.goog-date-picker-button {
  border: 0;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  font-size: 10px;
  height: 20px;
  left: 2px;
  line-height: 20px;
  margin: 0 auto;
  padding-left: 0;
  position: relative;
  text-align: center;
  width: 20px;
  background-color: transparent;
  transition: background-color 0.1s linear;
}
.goog-date-picker-button:hover {
